import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NavController, MenuController, ToastController, AlertController, LoadingController } from '@ionic/angular';
import { AuthService } from 'src/app/services/auth.service';
import { NativeStorage } from '@ionic-native/native-storage/ngx';
import { AlertService } from 'src/app/services/alert.service';

@Component({
  selector: 'app-tech-login',
  templateUrl: './tech-login.page.html',
  styleUrls: ['./tech-login.page.scss'],
})
export class TechLoginPage implements OnInit {
  public onTechLoginForm: FormGroup;
  username:any;
  password:any;
  constructor(public navCtrl: NavController,
    public menuCtrl: MenuController,
    public toastCtrl: ToastController,
    public alertCtrl: AlertController,
    public loadingCtrl: LoadingController,
    private formBuilder: FormBuilder,
    private authService: AuthService,
    private storage: NativeStorage,
    private alertService: AlertService) { }

ionViewWillEnter() {
    this.menuCtrl.enable(false);
  }

   ngOnInit() {
   this.onTechLoginForm = this.formBuilder.group({
      'username': [null, Validators.compose([
        Validators.required,
      ])],
      'password': [null, Validators.compose([
        Validators.required, 
      ])]
    });
  }

   login() {

     var data = this.onTechLoginForm.value;
     var username = data.username;
     var password = data.password;
     var body ='';
     console.log(username,password);
    this.authService.techLogin(username,password,body).subscribe(
      data => {
        console.log(data);
        console.log(data['result'].login.session, data['result'].modules[16].id);
        if (data['success']) {
          this.authService.storeTechData(data['result'].login.session, data['result'].modules[16].id, data['result'].login.userid);
          this.alertService.presentToast('Logged in successfully');
          this.navCtrl.navigateRoot('tech-home');
        } else {
          this.alertService.presentToast(data['success']);
          return false;
        }
      }
    );
  }

  back(){
    this.navCtrl.navigateRoot('/');
  }
}
